<template>
  <main class="card" role="region" aria-label="Секундомер">
    <h1 class="title">Регламент: <span style="color: red; font-weight: bold">60</span> минут</h1>
    <div id="display" class="timer" aria-live="polite" aria-atomic="true">{{ formattedTime }}</div>
    <div class="controls">
      <v-btn
          class="button"
          :color="startTimer ? 'red-darken-1' : 'green-darken-1'"
          @click="toggle"
          :class="isRunning ? 'stop' : 'start'">
        {{ isRunning ? 'Стоп' : 'Старт' }}
      </v-btn>
      <v-btn
          @click="reset"
          class="ghost"
          title="Сброс">
        Сброс
      </v-btn>
    </div>
  </main>
</template>

<script>
export default {
  name: 'StopwatchTimer',
// Options API
  data() {
    return {
      elapsedSec: 0,
      intervalId: null,
      startTimer: false
    };
  },
  computed: {
    isRunning() {
      return this.intervalId !== null;
    },
    formattedTime() {
      const pad2 = (n) => String(n).padStart(2, '0');
      const h = Math.floor(this.elapsedSec / 3600);
      const m = Math.floor((this.elapsedSec % 3600) / 60);
      const s = this.elapsedSec % 60;
      return `${pad2(h)}:${pad2(m)}:${pad2(s)}`;
    },
  },
  methods: {
    start() {
      if (this.intervalId !== null) return;
// Увеличиваем 1 сек каждую секунду
      this.intervalId = setInterval(() => {
        this.elapsedSec += 1;
      }, 1000);
      this.startTimer = true;
    },
    stop() {
      if (this.intervalId === null) return;
      clearInterval(this.intervalId);
      this.intervalId = null;
      this.startTimer = false;
    },
    toggle() {
      if (this.isRunning) this.stop(); else this.start();
    },
    reset() {
      this.stop();
      this.elapsedSec = 0;
    },
  },
  beforeUnmount() {
// очищаем таймер при размонтировании
    if (this.intervalId !== null) clearInterval(this.intervalId);
  },
};
</script>


<style scoped>
:root {
  --bg: #0f172a;
  --card: #111827;
  --text: #fff5ee;
  --muted: #9ca3af;
  --accent: #22c55e;
  --danger: #ef4444;
}

* {
  box-sizing: border-box;
}

body {
  font-family: Inter, system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial;
}

.card {
  width: min(270px, 90vw);
  border: 1px solid rgba(255, 255, 255, 0.06);
  backdrop-filter: blur(6px);
  border-radius: 5px;
  padding: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  color: var(--text);
}

.title {
  font-weight: 700;
  font-size: 1.25rem;
}

.timer {
  font-variant-numeric: tabular-nums;
  font-weight: 800;
  letter-spacing: 2px;
  font-size: clamp(30px, 8vw, 45px);
  text-align: center;
  color: var(--text);
  margin-bottom: 10px;
}

.controls {
  display: flex;
  gap: 12px;
  justify-content: center;
}
</style>